	module.exports = {"data":{"allMarkdownRemark":{"edges":[{"node":{"frontmatter":{"title":"About"},"html":"<p>This is a hybrid static site working as a single page application (SPA)\nfollowing the The <a href=\"https://developers.google.com/web/fundamentals/performance/prpl-pattern/\">PRPL Pattern</a>\nfor improved performance and user experience.</p>\n<p>The tool to check if you want a similar app is: <a href=\"https://www.gatsbyjs.org\">Gatsby</a>\nCombining the features of static sites together with tools such as React, Webpack,\nGraphQL and others, developers benefit greatly from:</p>\n<h2>Components</h2>\n<p>Everything, even pages in their simplest form are components:</p>\n<pre><code class=\"language-jsx\">const ContactPage = () => (\n  &#x3C;div>\n    &#x3C;h1>Contacts&#x3C;/h1>\n    ...\n  &#x3C;/div>\n);\n\nexport default ContactPage;\n</code></pre>\n<h2>Plugin architecture</h2>\n<p>Similarly to <a href=\"https://github.com/facebookincubator/create-react-app\">create-react-app</a>\none can start off without any configurations. Though, with the more requirements\nat hand for the project, various <a href=\"https://github.com/gatsbyjs/gatsby/tree/master/packages\">plugins</a>\ncan be added to extend the features of the site in many ways. Most interestingly,\nadding different data sources and making them query-able with GraphQL.</p>\n<h2>Data via GraphQL</h2>\n<p>It's possible to add data from various sources which are exposed in a single\nGraphQL endpoint during build time. This means that we can have a list of beers\nin this simple way:</p>\n<pre><code class=\"language-graphql\">query AllBeersQuery {\n  allBeersJson {\n    edges {\n      node {\n        Name_en\n        BeerStyle_en\n        Description_en\n      }\n    }\n  }\n}\n</code></pre>\n<p>More information is available in <a href=\"https://www.gatsbyjs.org/tutorial/part-four/\">this tutorial</a>.</p>\n<p>The repository of this <a href=\"https://github.com/kalinchernev/belgian-beers\">site is open source on github</a>.</p>"}}]}},"pathContext":{}}

